@use '../../common/styles/mixins' as *;
.filters {
  width: 100%;
  background: #fff;
  z-index: 9997;
  position: sticky;
  top: 0;
  transition: top .1s ease-out;

  &__header {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    align-items: center;
  }

  &__count {
    @media screen and (min-width: 1009px) {
      margin-right: auto;
    }

    &.loading {
      animation: skeleton-loading 1s linear infinite alternate;
      color: transparent;
    }
  }

  &__sortBtn {
    display: flex;
    align-items: center;
    gap: 5px;
    @media screen and (min-width: 1009px) {
      position: relative;
      margin-left: 20px;
    }

    @media screen and (max-width: 1008px) {
      order: 2;
    }

    #sortBtn {
      text-transform: uppercase!important;
    }

    &.active {
      #sortBtn {
        font-weight: 700;
        color:#222;
      }

      & .filters__sort-dropdown {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__filterBtn {
    @media screen and (max-width: 1008px) {
      order: 3;
    }
  }

  &__sort-dropdown {
    position: absolute;
    top: 120%;
    right: 0;
    z-index: 99999999;
    background: #fff;
    border: 1px solid #000;
    box-shadow: 3px 2px 5px 0 rgba(0,0,0,.125);
    padding: var(--padding-mobile, 2rem);
    visibility: hidden;
    opacity: 0;
    transition: opacity .2s ease-in;

    @media screen and (max-width: 1008px) {
      top: 100%;
      left: 0;
      right: 0;
      border-left: none;
      border-right: none;
    }
    
    ul {
      display: flex;
      flex-direction: column;
      gap: 1rem;
    }
  }

  &__sort-option {
    width: max-content;
    &.active {
      font-weight: 700;
      color: #222;
    }
  }

  &__sort-backdrop {
    height: 100vh;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99999998;
    background: transparent;
    visibility: hidden;

    @media screen and (max-width: 1008px) {
      top:100%;
      right: initial;
      left: 50%;
      transform: translateX(-50%);
      width: 100vw;
      background: rgba(0,0,0,.35);
      position: absolute;
    }
  }

  &__sortBtn.active &__sort-backdrop {
    visibility: visible;
  }
  
  &__drawer {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    max-width: 400px;
    height: 100vh;
    background: #fff;
    box-shadow: 0 0 15px rgba(0,0,0,.35);
    transform: translateX(110%);
    transition: transform .3s ease-out;
    z-index: 9999999999;
    overflow-y: scroll;

    @media screen and (max-width: 1008px) {
      height: 60vh;
      max-width: none;
      width: 100vw;
      top: calc(var(--header-height) + var(--plp-subnav-height) + var(--filters-bar-height) + var(--announcement-banner-height) + var(--ge-free-shipping-container));
      box-shadow: none;
      border: none;
      border-bottom: 1px solid #000;
      border-top: 1px solid #000;
      z-index: 999999999;
    }
  }

  &__drawer-header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: calc(var(--ge-free-shipping-container, 0px) + var(--announcement-banner-height, 30px) + var(--header-height, 69px));
    width: 100%;
    padding: 0 20px;
    border-bottom: 1px solid var(--black);

    @media screen and (max-width: 1008px) {
      display: none;
    }
  }

  &__drawer-closeBtn {

    svg {
      width: 25px;
      height: 25px;
      path {
        stroke-width: 18;
      }
    }
  }

  &__drawer-body {
    padding: 35px 20px;
    @media screen and (max-width: 1008px) {
      padding: 35px var(--padding-mobile, 20px)
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &--wrapper {
      width: 100%;
      padding-bottom:20px
    }
    &-btn--arrow {
      display: flex;

      svg {
        width: 15px;
        height: 15px;
        color: #000
      }
    }
  }

  &__options-group {
    display: grid;
    grid-template-rows: 0fr;
    overflow: hidden;
    transition: grid-template-rows 0.4s ease;
    ul {
      padding-top: 15px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      height: auto;
      max-height: 100%;
      width: 100%;
      row-gap: 10px;
    }

    li {
      &:not([data-key="filter.v.option.size"]) {
        text-transform: capitalize!important;
      }
      &.active {
        font-weight: 700;
      }

      &.disabled {
        cursor: not-allowed!important;
        color: var(--silver)
      }
    }
  }

  &__options-wrapper {
    min-height: 0;
  }

  &__drawer-footer {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin: 5rem 0;
    padding: 0 20px;

    .Button {
      display: flex;
      flex-grow: 1;
      justify-content: center;
      padding: 1rem 0;
    }
  }

  &__drawer-backdrop {
    background: rgba(0,0,0,.35);
    height: 100vh;
    left: 0;
    opacity: 0;
    position: fixed;
    top: 0;
    transition: opacity .3s ease;
    visibility: hidden;
    width: 100%;
    z-index: 99999999;

    @media screen and (max-width: 1008px) {
      top: calc(var(--header-height) + var(--plp-subnav-height) + var(--filters-bar-height) + var(--announcement-banner-height) + var(--ge-free-shipping-container));
    }
  }

  &__bar, &__selected  {
    padding: 0 var(--padding-mobile, 2rem);
    @media screen and (min-width: 990px) {
      padding: 0 var(--padding-desktop, 4rem);
    }
  }

  &__selected {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 1.25rem;
    margin-bottom: 1rem;
    margin-top: .5rem;

    &.loading {
      pointer-events: none;
    }

    .item-capitalized {
      text-transform: capitalize!important;
    }

    &--item {
      padding: 0.2rem 1rem;
      box-shadow: 0px 0px 0px 1px #000;
      cursor: pointer;
      display: flex;
      align-items: center;
      gap: 1rem;
      white-space: nowrap;
      font-size: 15px;

      svg {
        width: 10px;
        height: 10px;
      }

      &:hover {
        box-shadow: 0px 0px 0px 2px var(--silver);
      }
    }

    &--remove {
      font-size: 15px;
      &:only-child {
        display: none;
      }
      a {
        text-decoration: underline;
        text-underline-offset: 2px;

        &:hover {
          color: var(--silver)
        }
      }
    }
  }
}

.collapsable-container--open .filters__options-group {
  grid-template-rows: 1fr;
}

.collections.active-filters {
  .filters {
    &__filterBtn {
      font-weight: 700;
    }

    &__drawer {
      transform: translateX(0);
    }

    &__drawer-backdrop {
      visibility: visible;
      opacity: 1;
    }
  }
}

.shopify-section-header-sticky ~ main .filters__bar {
  top: calc(var(--header-height, 68px) - (var(--filters-top-offset, 2) * 2) + var(--plp-subnav-height));
}
.section-header.shopify-section-header-hidden ~ main .filters__bar {
  top: calc(var(--plp-subnav-height) - 2px);
}

.shopify-section-header-sticky ~ main .filters__drawer-header {
  min-height: calc(var(--header-height, 70px) - 0.5px);
}
.shopify-section-header-sticky.shopify-section-header-hidden ~ main .filters__drawer-header {
  min-height: calc(var(--header-height, 70px) - 1px);
}

@media screen and (max-width: 1008px) {
  .section-header.shopify-section-header-hidden ~ main .filters__drawer,
  .section-header.shopify-section-header-hidden ~ main .filters__drawer-backdrop {
    top: calc(var(--plp-subnav-height) + var(--filters-bar-height) - 2px);
  }
  .shopify-section-header-sticky ~ main .filters__drawer-backdrop,
  .shopify-section-header-sticky ~ main .filters__drawer {
    top: calc(var(--header-height, 68px) + var(--plp-subnav-height) + var(--filters-bar-height) - 4px);
  }
}

@keyframes skeleton-loading {
  0% {background-color: rgb(200, 200, 200)}
  100% {background-color: rgb(238, 238, 238)}
}