@mixin defaulFont($color, $weight) {
  font-weight: $weight;
  font-family: Roboto;
  color: $color;
  text-transform: uppercase;
}

.mobile-filters {
  width: 100%;
  min-height: 40px; 
  padding: 0 2rem 0 0;

  &__drawer {
    z-index: 9999999999;
    background: #fff;
    position: fixed;
    top: 0;
    right: 0;
    // transition: transform .45s cubic-bezier(.73,.08,.1,.97);
    box-shadow: 0 0 15px rgba(0, 0, 0, .35);
    display: flex;
    flex-direction: column;

    &--filters {
      max-width: 400px;
      transform: translateX(110%);
      height: 100vh;
      width: 100%;
  
      @media screen and (max-width: 1024px) {
        width: 100vw;
        max-width: none;
        height: 60vh;
      }
    }

    &__head {
      width: 100%;
      display: flex;
      align-items: center;
      padding: 0 20px;
      position: relative;
      justify-content: space-between;
      border-bottom: 1px solid var(--black);
      min-height: calc(var(--announcement-banner-height, 30px) + var(--header-height, 69px) - 1.5px);

      @media screen and (max-width: 1024px) {
        display: none;
      }
    }
  
    &__title {
      font-family: var(--body-font);
      color: var(--black)
    }
  
    &__icon {
      width: 30px;
      height: 30px;
    }

    &__body {
      flex-grow: 1;
      padding: 3.5rem 1.6rem 0 1.6rem;
      height: calc(100% - (var(--header-height)));
      overflow-y: auto;
      overflow-x: hidden;
      display: grid;
      grid-template-rows: repeat(2, min-content);

      @media screen and (max-width: 640px) {
        padding: 0;
      }
    }

    &__footer {
      display: flex;
      gap: 2rem;
      margin: 5rem 0;

      .btn-apply,
      .btn-clear {
        padding: 1rem 0;
        display: flex;
        justify-content: center;
        flex-grow: 1;
        max-width: calc(50% - 1rem);
      }
    }

    &--sort {
      height: auto;
      width: 100%;
      bottom: initial;
      top: calc(var(--header-height) + var(--plp-subnav-height) + var(--filters-bar-height) + var(--announcement-banner-height));
      transform: translateY(100%);

      .mobile-filters__drawer__body {
        border-top: 1px solid #000;
        border-bottom: 1px solid #000;
      }
    }

    &--sort &__body {
      padding: 0;   
    }
    
    &.active { transform: translate(0); }
  }
  
  &__list,
  &__accordian {
    height: 100%;
    max-height: 100%;
    width: 100%;
  }

  &__accordian &__list { padding-top: 15px; }

  &__item-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 20px;
    cursor: pointer;

    @media screen and (max-width: 640px) {
      padding: 1.5rem !important;
    }
  }

  &__item {
    font-family: var(--body-font);
    color: var(--black);
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item:hover {
    font-weight: 600;
  }

  &__item:hover path {
    stroke-width: 2;
  }

  &__accordian &__item {
    color: #222;
    padding-right: 15px;
    margin-bottom: 10px;
  }

  &__icon {
    width: 25px;
    height: 25px;
  }

  &__btn {
    display: flex;

    svg {
      width: 15px;
      height: 15px;
    }

    &--clear {
      margin-right: 7.5px;
      font-size: inherit;
      font-family: inherit;
      visibility: hidden;
    }

    &--plus {
      width: 25px;
      height: 25px;
      position: relative;

      &::after,
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        background: #4d4d4dff;
        display: inline-block;
        border-radius: 2px;
      }
      
      &::after {
        width: 2px;
        height: 15px;
        transition: transform .175s ease;
      }

      &::before {
        width: 15px;
        height: 2px;
      }
    }
  }

  &__item-wrapper.active &__btn--clear {
    visibility: visible;
  }

  &__group {
    display: flex;
    align-items: center;
  }

  &__filters,
  &__sort {
    color: var(--black);
    font-weight: 400;
    font-family: var(--body-font);
    min-height: inherit;
    flex: 1 1 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
  }

  &__sort {
    grid-column: 2/3;
    grid-row: 1
  }

  &__filters span {
    cursor: pointer;
  }

  &__filters span:hover {
    font-weight: 600;
  }

  &__drawer--sort &__list {
    padding: 20px;
  }

  &__drawer--sort &__item {
    justify-content: start;
    font-weight: 400;
    color: #000;

    &:not(:last-child) { margin-bottom: 10px; }

    &.active {
      color: #222;
      font-weight: 700;
    }
  }
  
  &__backdrop {
    background: rgba(0, 0, 0, .35);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999999;
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s ease;
    width: 100%;
    height: 100vh;
  }

  &__drawer.active ~ &__backdrop {
    opacity: 1;
    visibility: visible;
  } 

  .collapsable--open &__btn--plus::after {
    transform: translate(-50%, -50%) rotate(-.25turn);
  }

  &__applied-head {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
    font-size: 13px;
  }

  &__type {
    font-weight: 400;
    font-size: 10.5px;
    color: var(--text-color-light);
    text-transform: capitalize;
  }

  &__applied-item {
    font-size: 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 7.5px;
    }
  }

  &__applied-btn {
    font-weight: 400;
    font-size: 10px;
  }
}

@media screen and (max-width: 1024px) {
  .filters__result-count {
    display: flex;
    justify-content: start;
    align-items: center;
    flex: 1 1 0px;
  }

  .mobile-filters__filters {
    justify-content: end;
  }
}

.shopify-section-header-sticky ~ main .collections  .mobile-filters__drawer__head {
  min-height: calc(var(--header-height, 70px) - 0.5px);
}
.shopify-section-header-sticky.shopify-section-header-hidden ~ main .collections  .mobile-filters__drawer__head {
  min-height: calc(var(--header-height, 70px) - 2.5px);
}