.collapsable{overflow:hidden}@media screen and (max-width: 768px){.collapsable .collapsable{overflow:auto}}.collapsable-init{visibility:visible !important}.accordion-c{width:100%;border-bottom:1px solid rgba(0,0,0,.1)}.accordion-c__btn{font-family:var(--heading-font-small);font-weight:500;font-size:1.1rem;padding:0;margin:0;line-height:inherit;outline:none;border:none;background:rgba(0,0,0,0);display:flex;justify-content:space-between;width:100%;text-transform:uppercase;padding:1.5rem 0;cursor:pointer;color:#1c1c1c;align-items:center}.accordion-c__btn:focus{outline:none}.accordion-c--open .animation-container{grid-template-rows:1fr;visibility:visible;min-height:auto}.accordion-c__plus{position:relative;width:2rem;height:2rem;display:inline-block}.accordion-c__plus::after,.accordion-c__plus::before{content:"";display:inline-block;width:1px;height:1.25rem;position:absolute;top:50%;left:50%;background:#424242;transform:translate(-50%, -50%);transition:transform .6s ease}.accordion-c__plus::after{transition:all .75s ease;transform:translate(-50%, -50%) rotate(90deg)}.accordion-c__content{font-size:1.25rem;min-height:0}.accordion-c__content p{margin:0;font-size:1.2rem;color:#000;line-height:1.65;margin-bottom:.65rem}.accordion-c__content--dashed{display:flex;flex-direction:column}.accordion-c__content--dashed p{display:flex;align-items:center;text-transform:capitalize}.accordion-c__content--dashed p:not(:last-child){margin-bottom:.65rem}.accordion-c__content--dashed p::before{content:"-";position:relative;color:#000;font-size:inherit;margin-right:.5rem}.accordion-c__content-inner{padding-bottom:2rem}.accordion-c--open .accordion-c__plus::before{transform:translate(-50%, -50%) rotate(270deg)}.accordion-c--open .accordion-c__plus::after{transform:translate(-50%, -50%) rotate(270deg);opacity:0}.accordion-c--open~.animation-container{grid-template-rows:1fr;visibility:visible;min-height:auto}.animation-container{display:grid;grid-template-rows:0fr;transition:grid-template-rows .3s ease;visibility:hidden;min-height:0}.animation-container.active{grid-template-rows:1fr;visibility:visible;min-height:auto}.animation-container>*{overflow:hidden}