@use '../../common/styles/mixins' as *;

.collections {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0;

  &__head {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding: 7rem 0;
    text-align: center;

    h1 {
      font-size: 2.4rem;
      line-height: 1;
      letter-spacing: 0;
      margin-bottom: 1.8rem;
      @include respond(mid) {
        margin: 2rem 0;
      }
    }
  }

  &__description {
    line-height: 1.65;
    font-size: 1.4rem;

    p {
      margin: 0;
      color: #404040; 
    }
  }

  &__body {
    width: 100%;
    margin: 0 0 20px;
    display: flex;
    flex-direction: column;
    transition: opacity .35s ease;
    transition-delay: .35s;
    padding: 0 var(--padding-mobile, 2rem);
    align-items: center;
    @media screen and (min-width: 990px) {
      padding: 0 var(--padding-desktop, 4rem);
    }

    @include respond(tablet) {
      margin-top: 0;      
    }
  }

  &.loading &__body {
    display: none!important;
  }

  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0px 2px;
    overflow: hidden;
  }

  @include respond(tablet) {
    .collections__list {
      grid-template-columns: 1fr 1fr!important;
      gap: 3.5px;
    }
  }

  .collections__list{
    grid-template-columns: repeat(4, minmax(0,1fr));
  }
    
  @media only screen and (max-width: 600px) {
    .product-item__footer {
      min-height: 110px;
    }
    
    .product-item__footer .product-item__link {
      display: block !important;
      width: 100%;
    }
  }

  &__featured-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: transform .4s ease;
    backface-visibility: hidden;
    -webkit-font-smoothing: subpixel-antialiased;
    
    &, img {
      object-fit: cover;
      object-position: var(--focal-desktop);
      @media screen and (max-width: 980px) {
        object-position: var(--focal-mobile);
      }
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  .product-item[data-layout="with_image"] &__featured-image:hover {
    transform: scale(1.035);
  }

  &__list--featured-product &.product-item:first-child {
    grid-column: 1 / span 2;
  }

  &__list[data-layout="with_featured_product"] .product-item {
    @media screen and (min-width: 1140px) {
      &:nth-child(14) {
        grid-column: 1 / span 2;
        padding-left: 35%;
      }

      &:nth-child(15) {
        grid-column: 3 / span 2;
        padding-right: 35%;
      }
    }
  }
  
  &__group {
    display: flex;
    align-items: center;
  }

  &__collection-group {
    display: flex;
    grid-column: 1 / -1;

    &:last-child { margin-bottom: 60px; }

    @media screen and (max-width: 1024px) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 75px;
      padding: 0 12px;
    }
  }

  &__collection-group &__list {
    margin-bottom: 20px;
  }

  &__collection-group &__group {
    flex-direction: column;
    width: 80%;

    @media screen and (max-width: 1024px) {
      width: 100%;
    }
  }

  &__collection-group &__title {
    width: 20%;
    font-size: 1.3rem;
    font-weight: 700;
    text-transform: uppercase;
    padding-left: 7.5px;

    @media screen and (max-width: 1024px) {
      width: max-content;
    }
  }

  &__header {
    padding: 5rem 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__heading {
    font-size: 1.5rem;
    color: #444;
    font-family: 'Roboto';
  }

  &__loader {
    display: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 175px;
    position: relative;

    span {
      @include respond(mid) {
        width: 30px;
        height: 30px;
        border-width: 2.75px;
      }
    }
  }

  &.loading &__loader {
    display: flex!important;
  }

  &__loader.active { display: flex; }

  &__loader.active + &__load-more-btn { display: none; }

  &__load-more-btn {
    font-size: 1.2rem;
    margin-bottom: 7.5rem;
    @media screen and (max-width: 450px) {
      padding: 1rem 0!important;
      width: 100%!important;
      display: flex;
      justify-content: center;
    }
  }

  &__description { 
    max-width: 600px;
    text-align: center;
  }
  
  @include respond(mid) {
    &__head { padding: 4rem 0; }
  }

  &__hero {
    width: 100%;
    margin-top: var(--margin-top-desktop);
    @media screen and (max-width: 1008px) {
      margin-top: var(--margin-top-mobile)
    }

    &--bordered {
      padding: 0 4rem;
      @media screen and (max-width: 768px) {
        padding: 0 2rem;        
      }
    } 
    &--image {
      width: 100%;
      picture {
        display: block;
        height: auto;
      }

      img {
        display: block;
        width: 100%;
        object-fit: cover;
      }
    }
  }

  &__empty {
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 1rem;

    & > * {
      margin: 0
    }

    .Button {
      margin-top: 1.5rem
    }
    
    &.active {
      display: flex;
    }
  }

  &__promoBanner {
    position: relative;
    grid-column: span 2;

    @media screen and (max-width: 1008px) {
      padding-bottom: var(--padding-bottom-mobile, 0);
    }
    &--large {
      grid-row: span 2;
    }
  }

  &__promoBanner-media {
    width: 100%;
    height: calc(100% - 14.1rem);
    position: relative;
    picture {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: var(--focal-desktop);
        @media screen and (max-width: 980px) {
          object-position: var(--focal-mobile);
        }
        @media screen and (max-width: 1008px) {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
        }
      }
    }

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

    }

    @media screen and (max-width: 1008px) {
      padding-bottom: calc(10 / 7 * 100%);
      height: 0;
    }
  }

  &__promoBanner-content-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    & > * {
      margin: 0;
      color: inherit!important;
      text-align: center;
    }

    & > *:not(.collections__promoBanner-overlay):not(.collections__promoBanner-link) {
      max-width: 80%;
    }

    &--center {
      justify-content: center;
    }
    &--bottom {
      justify-content: flex-end;

      .contetn-filler {
        display: block!important;
      }
    }

    &--below {
      h2, .text, .Button {
        display: none;
      }
    }

    a.Button {
      z-index: 4;
      position: relative;
      &:not(:hover) {
        color: inherit!important;
      }
    }

    p, h2 {
      z-index: 2;
      position: relative;
    }

    .contetn-filler {
      display: none;
      min-height: 4rem;
      width: 100%;
    }
  }

  &__promoBanner-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 14.1rem);

    @media screen and (max-width: 1008px) {
      height: 0;
      padding-bottom: calc(10 / 7 * 100%);
    }
  }

  &__promoBanner-link {
    display: block!important;
    z-index: 3;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__promoBanner-footer {
    margin: 2rem 0;
    padding: 0 1rem;
    display: none;

    a:hover {
      color: var(--dark-silver)!important;
    }

    a:active {
      color: var(--silver);
    }

    &.content--below {
      display: block;
    }
  }

  &__promoBanner-overlay {
    display: block!important;
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: linear-gradient( rgba(0,0,0,0), 60%, rgba(0,0,0, var(--overlay, 0)) );
  }
}

@media screen and (min-width: 1024px) {
  .PageHeader {
    margin: 8.25rem 0;
  }
}

@keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}
@-webkit-keyframes spin {
  to { -webkit-transform: rotate(360deg); }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@-webkit-keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.labels {
  display: flex;
  gap: 0.3rem;
  flex-direction: column;
  margin-top: 0.3rem; 
}

.labels .label {
  font-weight: 500;
  font-family: Roboto;
  letter-spacing: var(--label-letter-spacing-desktop);
  text-transform: var(--label-text-transform);
  font-size: var(--label-text-font-size-desktop); 
  color: #424242;
  padding: var(--label-padding-desktop)
  
} 

@media screen and (max-width: 1025px) {
  .labels .label{
    font-size: var(--label-text-font-size-mobile);
    letter-spacing: var(--label-letter-spacing-mobile);
    padding: var(--label-padding-mobile);
  }
  
}

.labels-wrapper {
  align-self: start;
}

.new-in-label {
  color: var(--new-in-color);
}

.limited-label {
  color: var(--limited-edition-color);
}

.labels a {
  display: flex;
  line-height: initial;
}

.child_category {
  min-width: max-content;
  padding: 2rem 0 1.5rem 0;
}
.categories_list {
  display: flex;
  overflow-x: auto;
  gap: 4rem;
  padding-right: var(--padding-desktop, 4rem);

  @media screen and (max-width: 768px) {
    gap: 2rem;
  }

  &::-webkit-scrollbar {
    height: 6px;
  }
 
  &::-webkit-scrollbar-thumb {
    background-color: #fff;
  }
  &:hover {
    &::-webkit-scrollbar-thumb {
      background-color: var(--silver);
    }
  }
 
  &::-webkit-scrollbar-track {
    background-color: #fff;
  }
}

.active_subnav {
  min-width: max-content;
  display: flex;
}

.active_subnav a {
  padding: 2rem 0 2rem var(--padding-mobile, 2rem);
  font-weight: 600;
  @media screen and (min-width: 990px) {
    padding-left: var(--padding-desktop, 4rem);
  }
}

.categories_subnav {
  border-bottom: 1px solid #000;
  &--inner {
    display: flex;
    gap: 4rem;
    margin: 0 auto;
    width: 100%;
  }
}

a.child_category.active,
a.child_category:hover {
  text-decoration: underline;
  text-underline-offset: 2px;
  color: var(--dark-silver);
}

@media screen and (max-width: 750px) {
  .categories_subnav.desktop {
    display: none;
  }
  .categories_subnav.mobile {
    display: flex;
  }
}

@media screen and (min-width: 749px) {
  .categories_subnav.desktop {
    display: flex;
  }
  .categories_subnav.mobile {
    display: none;
  }
}

.categories_subnav.mobile .categories_list {
  padding: 0 var(--padding-desktop, 4rem);
}

@media screen and (max-width: 1024px) {
  .categories_subnav.mobile .categories_list {
    padding: 0 var(--padding-mobile, 1rem);
  }
}

.categories_subnav.mobile .active_subnav {
  display: none;
}

.plp-subnav {
  position: sticky;
  z-index: 9998;
  background: #fff;
  top: calc(var(--header-height, 69px) - 2px);
  transition: top .1s ease-out;
}

.section-header.shopify-section-header-hidden ~ main .plp-subnav {
  top: 0;
}

@media screen and (max-width: 750px) {
  .plp-subnav {
    top: calc(var(--header-height, 56px) - 2px) 
  }
  .section-header.shopify-section-header-hidden ~ main .plp-subnav {
    top: 0;
  }
}
.collections .container {
  display: none;
}

.collections .container.active {
  display: block;
}

.videoBanner {
  position: absolute;
  height: 150%;
  width: 300%;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-position: center;
  background-size: cover;
}

.videoBannerWrppaer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: 10;
  display: block!important;
}

.product-item--featured .overlay::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient( rgba(0,0,0,0), 60%, rgba(0,0,0, var(--overlay)) );
}

.Button--load-more {
  padding: 1rem 3rem;
}